	import md5 from "@/common/SDK/md5.min.js";
	import uniPopup from "@/components/uni-popup/uni-popup.vue"
	import { buziAPI } from '@/api';
	export default {
		components:{
		uniPopup	
		},
		watch: {
			time(n, o) {
				if (n == 0) {
					this.countDown = false;
				}
			}
		},
		data() {
			return {
				member: {
					password: '',
					confirmPassword:'',//确认密码
					passwordType:true,//密码是否可见
					confirmPasswordType:true//确认密码是否可见
				},
				phone: {
					name: '',
					imageCode: '',
					smsCode: ''
				},
				imageCode: '', //图形验证码
				countDown: false, //是否倒计时
				warningTitle: '', //警告文字
				showWarn: false, //是否显示警告文字
				time: '', //倒计时时间
				agree:false,//是否同意协议
			}
		},
		onLoad() {
this.getImageCode();
		},
		methods: {
			//获取短信验证码
			getSmsCode() {
				if (!this.phone.name) {
					this.showWarn = true;
					this.warningTitle = '请输入手机号';
					return false;
				}
				if (this.phone.name) {
					let myreg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
					if (!myreg.test(this.phone.name)) {
						this.showWarn = true;
						this.warningTitle = '请输入正确的手机号';
						return false;
					} else {
						this.showWarn = false;
						this.warningTitle = '';
						buziAPI.getSmsCode(
							{
								mobile: this.phone.name,
								type: 13,
								verifyCode: this.phone.imageCode,
								timestamp: this.$localstorageFactory.get('get_time'),
								coonType: 5,
								cityId: '010'
							},
							res => {
								if (res.msg == '验证码发送成功') {
									let that = this;
									this.countDown = true;
									this.time = 59;
									setInterval(function() {
										that.time--;
									}, 1000);
								}
								uni.showToast({
									icon: 'none',
									title: res.msg
								});
							}
						);
					}
				}
			},
			//获取图形验证码
			getImageCode() {
				let that = this;
				this.$localstorageFactory.set('get_time',new Date().getTime())
				that.imageCode = 'https://m.ehaoyao.com/front_api/user/v1/mobile/account/getVerifyCode.json?timestamp='+this.$localstorageFactory.get('get_time')
			},
			//是否同意协议
			agreenClick(type){
				this.agree = type
				this.$refs.popup.close()
			},
			//注册
			registerClick(){
				if (!this.phone.name) {
					this.showWarn = true;
					this.warningTitle = '请输入手机号';
					return false;
				}
				if (this.phone.name) {
					let myreg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
					if (!myreg.test(this.phone.name)) {
						this.showWarn = true;
						this.warningTitle = '请输入正确的手机号';
						return false;
					}
				}
				if (this.member.password.length<6||this.member.password.length>20) {
					this.showWarn = true;
					this.warningTitle = '密码长度不符合规定';
					return false;
				}
				if (this.member.password!=this.member.confirmPassword) {
					this.showWarn = true;
					this.warningTitle = '两次输入的密码不一样';
					return false;
				}
				if (!this.agree) {
					this.showWarn = true;
					this.warningTitle = '请先阅读并同意《好药师用户注册协议》';
					return false;
				}
				this.showWarn = false;
				this.warningTitle = '';
				buziAPI.register(
					{
						mobile: this.phone.name,
						mobileCode: this.phone.smsCode,
						uniqueIdentifier: 'pc_uniqueIdentifier',
						password: this.member.password,
						coonType: 5,
						cityId: '010'
					},
					res => {
						if (res.msg == '注册成功!') {
							this.countDown = false;
							this.common.switchTab('./home')
						}
						uni.showToast({
							icon: 'none',
							title: res.msg
						});
					}
				);
			}
		}
	}
